@import '../common/variables.scss';

.main-section__button {
  font-size: 18px;
  font-weight: bold;
  color: white;
  border: 3px solid #fff;
  border-radius: 30px;
  background-color: transparent;
  text-decoration: none;
  padding: 15px 40px;
  margin-top: 7%;
  width: 100%;
}

.main-section__app {
  width: 87%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-section__title {
  display: none;
}

.main-section__gadgets {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10% 0;
  width: 100%;
}

.main-section__gadget {
  width: 60px;
  height: 60px;
}

.main-section__gadget--apple {
  background-image: url(./../images/apple-logopng.png);
  background-position: top left;
  background-repeat: no-repeat;
  background-size: 47px 56px;
}

.main-section__gadget--android {
  background-image: url(./../images/android-logo.png);
  background-position: center 8px;
  background-repeat: no-repeat;
  background-size: 43px 47px;
}

.main-section__gadget--microsoft {
  background-image: url(./../images/microsoft-logo.png);
  background-position: right 12px;
  background-repeat: no-repeat;
  background-size: 44px 41px;
}

.main-section__text {
  font-size: 14px;
  color: #fff;
}

.main-section__text {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-section__wrapper {
  margin-top: 420px;
  padding-bottom: 10%;
}

.main-section__photo {
  display: none;
}

.main-section__brands {
  width: 100%;
}

@media (min-width: $tablet-width) {
  .main-section__title {
    display: flex;
    font-size: 40px;
    color: #fff;
    font-weight: normal;
    line-height: 1.2;
    text-align: center;
    width: 75%;
  }

  .main-section__wrapper {
    width: 50%;
    margin-top: 13%;
    padding-bottom: 4%;
  }

  .main-section__gadgets {
    margin: 5% 0;
  }
}

@media (min-width: $desktop-width) {
  .main-section__app {
    width: 56%;
  }
}